.Widget {
    position: relative;
    display: inline-block;
    vertical-align: top;
}

.Mode {
    border-bottom: 1px solid #dee2e6;
}

.DragMode {
    border: solid rgba(0, 0, 0, 0.25) 1px;
}

.Delete {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.5);
    z-index: 999;
}