.Weather{
    position: absolute;
    width: 95%;
    height: 90%;
    margin: 2.5%;
    padding-top: 1rem;
    background-color: white;
    border-radius: 5px;
    box-shadow: 3px 3px 5px 0px rgba(63,63,63,0.25);
    text-align: center;
}

.Weather div {
    z-index: 100;
}

.Weather img {
    background-color: white;
    border-radius: 50%;
    /* margin: 10px; */
}

.Info {
    padding-top: 2.25rem;
}

.Text {
    width: 95%;
    margin: 2.5%;
    text-align: center;
}

.Header {
    position: absolute;
    width: 95%;
    height: 2.25rem;
    margin: 2.5%;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    background-color: #292b2c;
    z-index: 1;
}